.view_shop.empty {
  display: -ms-flexbox;
  display: flex;
  padding-top: 1rem;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-direction: column;
  flex-direction: column;
  color: #999;
}
.view_shop.empty .pic {
  display: inline-block;
  width: 30%;
  height: auto;
}
.view_shop.empty .btn {
  padding: 0 0.3rem;
  background: #fe6a3c;
  font-size: 0.16rem;
  height: 0.4rem;
  line-height: 0.4rem;
}
.cp_shopTab {
  display: -ms-flexbox;
  display: flex;
  overflow-y: hidden;
  height: 100%;
}
.cp_shopTab .tab {
  width: 0.8rem;
  text-align: center;
  height: 100%;
  overflow-y: auto;
  background: #eee;
}
.cp_shopTab .tab .tab_item {
  display: block;
  line-height: 0.5rem;
  background: #eee;
  color: #666;
}
.cp_shopTab .tab .tab_item.active {
  background: #fff;
  color: #fe6a3c;
}
.cp_shopTab .content {
  -ms-flex: 1 1;
  flex: 1 1;
  background: #fff;
  overflow-y: auto;
  height: 100%;
  padding: 0 0.1rem;
}
.cp_shopTab .content .pro_type {
  line-height: 0.5rem;
}
.cp_shopTab .content .pro_type .all {
  float: right;
  color: #999;
}
.cp_shopTab .content .pro_box {
  position: relative;
}
.cp_shopTab .content .pro_item {
  width: 33%;
  display: inline-block;
  text-align: center;
  padding-top: 0.1rem;
}
.cp_shopTab .content .pro_tilte {
  display: block;
  line-height: 0.3rem;
}
.cp_shopTab .content .pro_pic {
  width: 0.6rem;
  height: 0.6rem;
}
.home_nav {
  display: table;
  width: 100%;
  padding: 0.1rem 0 0.05rem;
}
.home_nav .cp_icon {
  width: 0.5rem;
  height: 0.5rem;
}
.home_nav .nav_item {
  width: 25%;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  color: #666;
}
.home_nav .nav_item .title {
  display: block;
  line-height: 0.4rem;
}
.home_nav .icon_nav_1 {
  background: url('../assets/img/icon_nav_01_new.png') no-repeat;
  background-size: 100%;
}
.home_nav .icon_nav_2 {
  background: url('../assets/img/icon_nav_02_new.png') no-repeat;
  background-size: 100%;
}
.home_nav .icon_nav_3 {
  background: url('../assets/img/icon_nav_03_new.png') no-repeat;
  background-size: 100%;
}
.home_nav .icon_nav_4 {
  background: url('../assets/img/icon_nav_04_new.png') no-repeat;
  background-size: 100%;
}



