.good_list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.good_box {
  width: 49%;
  font-size: 0.12rem;
  background: #fff;
  margin-bottom: 0.08rem;
}
.good_box .good_img {
  width: 100%;
  height: auto;
  max-height: 1.6rem;
}
.good_box p {
  margin: 0;
  line-height: 0.2rem;
  max-height: 0.4rem;
  overflow: hidden;
  padding: 0 0.05rem;
}
.good_box .title {
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.good_box .price {
  position: relative;
  font-size: 0.16rem;
  color: #fe6a3c;
  line-height: 0.3rem;
}
.good_box .price:before {
  position: relative;
  content: '￥';
  font-size: 0.12rem;
  -webkit-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(0.5);
}



